<template>
  <div>
    <div class="w-full bg-[skyblue] px-[2%] pb-10 pt-10 box-border">
      <p class="font-extrabold text-center text-[30px] mb-10">한국관광공사 관광지 사진 정보</p>
      <!-- Tap filter -->
      <div class="max-w-7xl m-auto flex flex-wrap gap-x-[1%]">

        <ul class="flex flex-wrap justify-center rounded-md border box-border p-4 basis-full mb-4 gap-x-4">
          <li @click="cateName='전체'" class="hover:text-[#fff] duration-500 border rounded pt-6 cursor-pointer text-xs md:text-base basis-[20%] sm:basis-[15%] md:basis-[10%] lg:basis-[8%] text-center mb-2 sm:mb-0" :class="cateName === '전체' && 'font-extrabold'">전체보기</li><!--:class="cateName === '전체' && 'font-extrabold'"-->

          <li @click="cateName = e.galPhotographyLocation" v-for="e in cateList()" :key="e" class="tracking-wide  items-center border rounded cursor-pointer text-xs md:text-base basis-[20%] sm:basis-[15%] md:basis-[15%] lg:basis-[10%] text-center pt-[8px] m-2 sm:bm-0 transition-all hover:text-white hover:bg-orange-400 duration-700" :class="cateName === e.galPhotographyLocation && 'font-extrabold'">{{ e.galPhotographyLocation }}</li>
        
          <!-- hover:font-extrabold mb-1 py-1 px-5  border rounded transition-all hover:text-white hover:shadow-[inset_-60px_0_0_0_orange,inset_60px_0_0_0_orange] duration-700 -->

          <li class="items-center border rounded cursor-pointer text-xs md:text-base basis-[30%] sm:basis-[15%] md:basis-[15%] lg:basis-[10%] text-center p-3 mb-2 sm:bm-0 font-extrabold hover:text-white duration-500"><button @click="open_page(HOMEPAGE_URL)">한국관광공사 홈페이지</button></li>
        </ul>
      </div>
    </div>
    <!-- <div class="basis-full flex justify-around bg-[lightblue] text-white pb-1">
      <ul class="flex justify-center ">
        <li v-for="e in pageList" :key="e" class="text-[20px] cursor-pointer px-1 m-2 hover:shadow-[inset_0_-60px_0_0_orange] duration-700" 
        :class="e === page && 'font-extrabold text-[#225771]'" @click="page = e; getItem()">{{ e }}</li>
      </ul> -->
       <!-- cursor-pointer px-1 m-2 hover:shadow-[inset_0_-60px_0_0_orange] duration-700  -->
    <!-- </div> -->
    <!-- 스와이퍼 시작 -->
    <swiper :modules="modules" @swiper="onSwiper" @slideChange="onSlideChange" :scrollbar="{ draggable: true }" :navigation="{ clickable: true }" :slides-per-view="1" class="mb-10 h-[600px]" 
      >
      <!--:pagination="{ clickable: true }" "
      :space-between="50"
      -->
      <!-- <swiper-slide></swiper-slide> -->
      <!-- <swiper-slide>Slide 2</swiper-slide>
      <swiper-slide>Slide 3</swiper-slide>
       -->
      
      <!-- sm:basis-[49%] lg:basis-[32.5%] -->
      <!-- cateItem() -->
      <swiper-slide v-for="e in cateItem()" :key="e" class="text-center bg-white sm:basis-[49%] lg:basis-[32.5%] mb-4 box-border border rounded-md p-5 m-5 !h-[500px]">
        <span class="bm-4 text-lg text-[25px] font-extrabold mb-5 tracking-wider">{{ e.galTitle }}</span>
        <img :src="e.galWebImageUrl" :alt="e.galWebImageUrl" class="m2-auto w-full">
        <ul class="mt-5 text-center text-[18px]">
          <li class="mb-5"><span class="font-extrabold">포토그래퍼</span> : {{ e.galPhotographer }}</li>
          <li class="mb-5"><span class="font-extrabold">위치</span> : {{ e.galPhotographyLocation }}</li>
          <li class="mb-5"><span class="font-extrabold">작성(월)</span> : {{ e.galPhotographyMonth }}</li>
          <!-- <li class="mb-5"><span class="font-extrabold">글 수정</span> : {{ e.galModifiedtime }}</li>
          <li class="mb-5"><span class="font-extrabold">글 작성</span> : {{ e.galCreatedtime }}</li> -->
          <!-- <li class="font-bold my-5 text-[22px] text-[#f89b00] cursor-pointer"><button @click="open_page(HOMEPAGE_URL)">홈페이지</button></li> -->
        </ul>
        <router-link :to="'/DetailPage/'+(e.galContentTypeId)" propsData="test" @click="ResultList(e.galContentTypeId)" class="cursor-pointer font-bold text-[18px] hover:underline">자세히 보기</router-link>
      </swiper-slide>
    </swiper>
    <!-- 페이지 리스트 -->
    <div class="basis-full flex justify-around bg-[lightblue] text-white -mt-7">
      <ul class="flex justify-center">
        <li v-for="e in pageList" :key="e" class="cursor-pointer px-1 m-2 text-[20px] hover:shadow-[inset_0_-60px_0_0_orange] duration-700 rounded" :class="e === page && 'font-extrabold text-[#225771]'" @click="page = e; getItem()">{{ e }}</li>
      </ul>
    </div>
  </div>  
</template>

<!-- 스크립트 시작 -->
<script>

import axios from 'axios'

 // import Swiper core and required modules
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';

// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from 'swiper/vue';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';
// import DataList from '@/assets/tourist.json';
export default {
  name: 'App',
  components: {
      Swiper,
      SwiperSlide,
    },
   
    setup() {
      const onSwiper = (swiper) => {
        console.log(swiper)
      }
      const onSlideChange = () => {
        console.log('slide change');
      }
      return {
        onSwiper,
        onSlideChange,
        modules: [Navigation, Pagination, Scrollbar, A11y],
      };
    },

  data(){
    return {
        test: "테스트",
        pageList: 10,
        page: 1,
        totalCount: 0,
        listItem: [],
        cateName: '전체',
        HOMEPAGE_URL: "http://tong.visitkorea.or.kr"
      }
    },
  
 methods:{
  ResultList(e){
    return this.listItem.filter((data)=>{
      if(data.galContentTypeId === e){
        return this.session(data)
      }
    })
  },
  session(data){
    sessionStorage.setItem('data', JSON.stringify(data));
  },
  open_page(url){
    window.open(url);
  },
    getItem: function(){
      axios.get(`https://apis.data.go.kr/B551011/PhotoGalleryService1/galleryList1?serviceKey=Fag5ltUiVOMqlOb7r2X7nJWRY4JRoCTu8jtWwcjMob9oY1L%2Ffo3uf1%2FTc62pFVIIpFpxdaQdHC%2BhSM87yRcpBg%3D%3D&numOfRows=15&pageNo=${this.page}&MobileOS=ETC&MobileApp=AppTest&arrange=A&_type=json`)

      .then((res)=>{
        this.listItem = res.data.response.body.items.item;
      })
      .catch((error)=>{
        console.log(error)
      })
    },
    // 필터 코드
      cateList(){
        return this.listItem.filter((item,e)=>{
          return(
            this.listItem.findIndex((item2)=>{
              return item.galPhotographyLocation === item2.galPhotographyLocation
            }) === e
          )
        })
      },
      cateItem(){
        return this.listItem.filter((data)=>{
          if(this.cateName !== '전체'){
            return data.galPhotographyLocation === this.cateName
          }else{
            return data.galPhotographyLocation
            }
          })
        }
     },
    
    
    created(){
      axios.get(`https://apis.data.go.kr/B551011/PhotoGalleryService1/galleryList1?serviceKey=Fag5ltUiVOMqlOb7r2X7nJWRY4JRoCTu8jtWwcjMob9oY1L%2Ffo3uf1%2FTc62pFVIIpFpxdaQdHC%2BhSM87yRcpBg%3D%3D&numOfRows=15&pageNo=1&MobileOS=ETC&MobileApp=AppTest&arrange=A&_type=json`)

      .then((res)=>{
        this.listItem = res.data.response.body.items.item
        console.log(res)
      })
      .catch((error)=>{
        console.log(error)
      })
      console.log(this.listItem)
    }
 
}
</script>

<style>
  /* 원스토어 모바일 팝 */
  @font-face {
      font-family: 'ONE-Mobile-POP';
      src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/ONE-Mobile-POP.woff') format('woff');
      font-weight: normal;
      font-style: normal;
  }
  div, ul, p{ font-family: 'ONE-Mobile-POP'; }
  .swiper-button-next{ opacity: 0.5;}
  .swiper-button-next:hover{ opacity: 1.0;}
  .swiper-button-prev{ bottom:1000px; opacity: 0.5;}
  .swiper-button-prev:hover{ opacity: 1.0;}
</style>
